.container-page{
    position: relative;
    width: 60%;
    margin: 40px auto;
}
.container-page .article-content{
    line-height: 2.5;
}
.container-page .edit-article{
    position: absolute;
    padding: 5px 10px;
    border-radius: 30px;
    color: #333;
    border: 1px solid #999;
    right: 0;
    top: 70px;
    text-decoration: none;
}
.article-footer{
    display: -ms-flexbox;
    display: flex;
    font-size: 13px;
    color: #666;
    -ms-flex-pack: justify;
        justify-content: space-between;
    margin-top: 30px;
    margin-bottom: 30px;
}




