body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
html,body,#root,#root>div{
  height: 100%;
}
p,h1,h2,h3,h4,h5,html,body,ul,ol,li{
  margin: 0;
  padding: 0;
}
*{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
ul,ol,li{
  list-style: none;
}
a{
  text-decoration: none;
}



